<template>
	<section>
		<h2>2017年</h2>
		<ul>
			<li class="ls_total_time">5月06日 星期五</li>
			<li class="ls_list c">
				<div class="ls_list_mb"  @click="clickListHandler($event)"></div>
				<time>12:30</time>
				<p class="ls_list_title">听说只支持十五个字大大大大大</p>
				<span>报名中</span>
				<i></i>
				<div v-show="flag" class="ls_list_detail">
					<p class="c"><span class="ls_list_detail_tips">报名人数：</span><span>999人</span></p>
					<p class="c"><span class="ls_list_detail_tips">报名上限：</span><span>9999人</span></p>
					<p class="c"><span class="ls_list_detail_tips">起始筹码：</span><span>50000</span></p>
					<p class="c"><span class="ls_list_detail_tips">起始盲注：</span><span>5/10</span></p>
					<p class="c"><span class="ls_list_detail_tips">涨盲时间：</span><span>10分钟</span></p>
					<p class="c"><span class="ls_list_detail_tips">延迟报名：</span><span>25个级别</span></p>
					<p class="c ls_list_full_p"><span class="ls_list_detail_tips">比赛说明：</span><span class="ls_list_detail_des">Lorem ipsum dolor sit amet,consectetur adipiscing elit. Aenean euismod bibendum laoreet. Poroin elit. Aenean euismod bibendum laoreet. Poroin</span></p>
				</div>
			</li>
			<li class="ls_list c"><div class="ls_list_mb"  @click="clickListHandler($event)"></div><time>12:30</time><p class="ls_list_title">主赛Day1 A组</p><span>报名中</span><i></i></li>
			<li class="ls_list c"><div class="ls_list_mb"  @click="clickListHandler($event)"></div><time>12:30</time><p class="ls_list_title">主赛Day1 A组</p><span>报名中</span><i></i></li>
		</ul>
		<ul>
			<li class="ls_total_time">5月06日 星期五</li>
			<li class="ls_list c"><div class="ls_list_mb"  @click="clickListHandler($event)"></div><time>12:30</time><p class="ls_list_title">听说只支持十五个字大大大大大</p><span>报名中</span><i></i></li>
			<li class="ls_list c"><div class="ls_list_mb"  @click="clickListHandler($event)"></div><time>12:30</time><p class="ls_list_title">主赛Day1 A组</p><span>报名中</span><i></i></li>
			<li class="ls_list c"><div class="ls_list_mb"  @click="clickListHandler($event)"></div><time>12:30</time><p class="ls_list_title">主赛Day1 A组</p><span>报名中</span><i></i></li>
		</ul>
		<ul>
			<li class="ls_total_time">5月06日 星期五</li>
			<li class="ls_list c"><div class="ls_list_mb"  @click="clickListHandler($event)"></div><time>12:30</time><p class="ls_list_title">听说只支持十五个字大大大大大</p><span>报名中</span><i></i></li>
			<li class="ls_list c"><div class="ls_list_mb"  @click="clickListHandler($event)"></div><time>12:30</time><p class="ls_list_title">主赛Day1 A组</p><span>报名中</span><i></i></li>
			<li class="ls_list c"><div class="ls_list_mb"  @click="clickListHandler($event)"></div><time>12:30</time><p class="ls_list_title">主赛Day1 A组</p><span>报名中</span><i></i></li>
		</ul>
	</section>
</template>

<script>
	export default {
		data(){
			return {
				flag:false
			}
		},
		methods:{
			clickListHandler(event){
				var targetDom = event.target;
				while(targetDom.nodeName.toLowerCase() !== 'li'){
					targetDom = targetDom.parentNode
				}
				if(this.judgeClassName(targetDom,'active')){
					this.removeClassName(targetDom,'active')
				}else{
					this.addClassName(targetDom,'active')
				}
				this.flag = !this.flag;
				var className = targetDom.className;
			},
			judgeClassName(dom,name){
				var cn = dom.className;
				var arr = cn.split(' ');
				for(var i = 0;i<arr.length;i++){
					if(arr[i] === name){
						return true;
					}
				}
				return false;
			},
			removeClassName(dom,name){
				var cn = dom.className;
				var arr = cn.split(' ');
				var nowArr = arr.filter(function(index){
					if(index === name){
						return false
					};
					return true;
				})
				dom.className = nowArr.join(' ');
			},
			addClassName(dom,name){
				if(this.judgeClassName(dom,name)){
					return false;
				}
				dom.className += (' ' + name)
			}
		}
	}
</script>

<style scoped lang='scss'>
	@import "./../assets/sass/function.scss";
	$color:#51d1b1;
	h2{
		height:40px;
		line-height:40px;
		color:$color;
		font-size:px2em(32px);
		text-align:center;
		background:#202737;
	}
	.ls_total_time{
		height:30px;
		line-height:30px;
		font-size:px2em(24px);
		color:$color;
		background:#243044;
		text-indent:px2em(28px);
	}
	.ls_list{
		line-height:48px;
		border-bottom:1px solid #151820;
		border-top:1px solid #2f3647;
		color:#ffffff;
		position:relative;
		font-size:px2em(24px);
		time{
			float:left;
			width:px2em(160px);
			text-indent:px2em(60px);
		}
		.ls_list_title{
			float:left;
			width:px2em(400px);
			overflow:hidden;
			white-space:nowrap;
			text-overflow:ellipsis;
		}
		span{
			float:left;
		}
		i{
			float:left;
			height:48px;
			padding:0 5px;
			margin-left:10px;
			background:url(./../assets/images/small_arr.png) no-repeat center / contain;
			transition:all .3s;
		}
	}
	.ls_list.active{
		i{
			transform:rotate(180deg);
		}
	}
	.ls_list_mb{
		position:absolute;
		top:0;left:0;
		width:100%;
		height:48px;
	}
	.ls_list_detail{
		width:100%;
		line-height:1.5;
		overflow:hidden;
		p{
			float:left;
			width:50%;
		}
		p.ls_list_full_p{
			width:100%;
		}
		span{
			float:left;
		}
	}
	.ls_list.outtime{
		color:gray;
	}
	.ls_list_detail_des{
		max-width:65%;
	}
	.ls_list_detail_tips{
		color:#717989;
		text-indent:px2em(60px);
	}
	.slide-enter-active {
	  transition: all .3s ease;
	}
	.slide-leave-active {
	  transition: all .3s;
	}
	.slide-enter, .slide-leave-to
	/* .slide-fade-leave-active for below version 2.1.8 */ {
	  height:0;
	  opacity: 0;
	}
</style>


